.about {
  display: -ms-flexbox;
  display: flex;
}

.navSpace {
  margin-left: 60px;
  -webkit-animation: fadeIn 1.5s;
          animation: fadeIn 1.5s;
}

@-webkit-keyframes fadeIn {
  from { opacity:0.7 }
}

@keyframes fadeIn {
  from { opacity:0.7 }
}

@media (max-width:768px) {
  .about {
    display: block;
  }

  .navSpace {
    margin-left: 0;
    margin-top: 0;
  }
}



