.navbar {
  display: inline;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 950;
}

.nav {
  position: fixed;
  top: 0;
  width: 60px;
  height: 100vh;
  background-color: black;
  z-index: 950;
  padding-left: 10px;
}

.navbar-text {
  color: white;
  font-size: 10px;
  margin: auto;
  cursor: pointer !important;
}

.nav-line {
  margin: auto;
  border-bottom: 1px solid rgba(255, 255 , 255, 0.6);
  height: 12vh;
  width: 60%;
}

.development {
  border: 1px solid red;
}

@media (min-width:768px) {
  .nav{
    left:0;
    -webkit-animation: slideLeft 0.5s;
            animation: slideLeft 0.5s;
  }
}

@media (max-width:768px) {
  .nav{
    left: 85%;
    width: 100%;
    height: 6vh;
    background-color: rgba(0,0,0,0.0);
    -webkit-animation: slideRightMobile 0.5s;
            animation: slideRightMobile 0.5s;
  }
  .navbar-text {
    color: var(--menu-bar-mobile);
  }
}

@-webkit-keyframes slideRightMobile {
  from { left:30% }
  to { left:85%}
}

@keyframes slideRightMobile {
  from { left:30% }
  to { left:85%}
}


@-webkit-keyframes slideLeft {
  from { width:300px;
         padding-left:125px; }
  to { width: 60px }
}


@keyframes slideLeft {
  from { width:300px;
         padding-left:125px; }
  to { width: 60px }
}



