.Github {
  font-family: monospace;
  padding-top: 10vh;
  height: 80vh;
  text-align: center;
}

.github-img {
  height: 20vh;
  width: auto;
}

.progress-bar-github{
  -webkit-animation: progress-run 2s;
          animation: progress-run 2s;
}

@-webkit-keyframes progress-run {
  from{width:0}
}

@keyframes progress-run {
  from{width:0}
}



