.btn-menu{
  border: 1px solid rgba(255,255,255,0.4);
  width: 40px;
  height: 40px;
  margin: auto;
  margin-top: 5vh;
  cursor: pointer !important;
}

.line-1{
  height:1px;
  border-bottom:1px solid rgba(255,255,255,0.8);
  margin-top: 7px;
  margin-bottom: 5px;
  margin-left: 10px;
  margin-right: 10px;
}

.line-2{
  height:1px;
  border-bottom:1px solid rgba(255,255,255,0.8);
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 5px;
  margin-left: 5px;
}

@media (max-width:768px) {
  .btn-menu{
    margin: auto;
    margin-top: 2vh;
    border: 1px solid var(--menu-bar-mobile);
  }
  .line-1 {
    border-bottom:1px solid var(--menu-bar-mobile);
  }
  .line-2 {
    border-bottom:1px solid var(--menu-bar-mobile);
  }
}



