.nav-full{
  position: fixed;
  top: 0;
  left: 0;
  width: 300px;
  height: 100vh;
  background-color: black;
  z-index: 950;
  -webkit-animation: slideRight 0.5s;
          animation: slideRight 0.5s;
}

@media (max-width:768px) {
  .nav-full {
    width: 100%;
    -webkit-animation: slideLeftMobile 0.5s;
            animation: slideLeftMobile 0.5s;
  }
}

@-webkit-keyframes slideLeftMobile {
  from {left:60%}
  to {left:0}
}

@keyframes slideLeftMobile {
  from {left:60%}
  to {left:0}
}

@-webkit-keyframes slideRight {
  from {left:-240px}
  to {left:0}
}

@keyframes slideRight {
  from {left:-240px}
  to {left:0}
}

.nav-center {
  -ms-flex-align: center;
      align-items: center;
  margin: auto;
  height: auto;
}

.nav-full-line {
  margin: auto;
  margin-top: 120px;
  border-bottom: 1px solid rgba(255, 255 , 255, 0.6);
  height: 1px;
  width: 60%;
}

.nav-tran {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: none;
  z-index: 949;
}



