.LoadPage{
  position: fixed;
  color: green;
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: monospace;
}

.fullLoad {
  width: 100%;
  height: 100vh;
  background-color: rgba(0,0,0,0.9);
}



